.send {
  grid-column: 1/-1;

  @include on-tablet {
    grid-column: span 4;
  }

  @include on-small-desktop {
    grid-column: span 5;
  }

  &__content {
    display: flex;
    flex-direction: column;

    margin-bottom: 80px;
  }

  &__text {
    display: flex;
    flex-direction: column;
  }

  &__title {
    font-family: Poppins, sans-serif;
    font-size: 32px;
    font-weight: 600;
    line-height: 48px;
    text-align: left;
    color: #fff;
    margin-bottom: 48px;
  }

  &__input {
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 40px;
    color: #fff;
    background-color: #2c2c2c;
    padding-bottom: 16px;
    resize: none;

    outline: none;
    border: none;
    border-bottom: 2px solid #fff;

    &:focus {
      border-bottom: 2px solid #144ed4;
    }

    &::placeholder {
      font-family: Poppins, sans-serif;
      font-size: 15px;
      font-weight: 600;
      line-height: 22.5px;
      text-align: left;
      color: #fff;
      opacity: 40%;
    }
  }

  &__button {
    color: #fff;
    background-color: #2e05e7;
    padding-block: 15px;
    width: 100%;

    border: none;
    border-radius: 8px;

    outline: none;

    text-decoration: none;
    text-align: center;

    font-family: Poppins, sans-serif;
    font-size: 15px;
    font-weight: 600;
    line-height: 22.5px;

    @include hover(transform, scale(1.1));
  }
}
